  color: var(--orange);
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}

.tank {
  height: 24%;
  width: calc(var(--wheel-depth) * 1vmin);
  top: 0;
  translate: 0 -25%;
  --depth: calc(var(--wheel-depth) * 1);
  --color: var(--grey-4);
}

.tank .cuboid__side:nth-of-type(2),
.tank .cuboid__side:nth-of-type(4) {
  background: repeating-linear-gradient(
