/* Hack to get the arms to show at rotation */
/*.arm--left .forearm-shell {
    transform: rotateY(calc(1deg + ((1 - var(--transform)) * 1deg)));
  }
  .forearm-shell {
    transform: rotateY(calc(-1deg + ((1 - var(--transform)) * 1deg)));
